import React from "react";
import "./loading.css";
import Logo from "../../assets/pixelcade.png";

const Loading: React.FC<{ isLoading: boolean }> = (props) => {
    const { isLoading } = props;
    return (
        <React.Fragment>
            {isLoading && (
                <div className="loading fade-in">
                    <img
                        className="pulse"
                        src={Logo}
                        alt="Pixelcade Companion Logo"
                    />
                </div>
            )}
        </React.Fragment>
    );
};

export default Loading;
