import React, { useState } from "react";
import { getDeviceUrl, updateMarquee } from "../../services";
import { MarqueeInfo } from "../../types/DeviceInfo.type";
import toast from "react-hot-toast";
import LazyLoad from "react-lazyload";
import { startCase } from "lodash";
import { useAppSettings } from "../../App";
import MarqueeDetailsModal from "../MarqueeDetailsModal";

type MarqueeItemProps = {
    system: string;
    marquee: MarqueeInfo;
    onClick?: Function;
};

const MarqueeItem: React.FC<MarqueeItemProps> = (props) => {
    const [error, setError] = useState<boolean>(false);
    const [modalOpen, setModalOpen] = useState<boolean>(false);
    const { marquee } = props;

    const { streamImages } = useAppSettings();

    const setMarquee = () => {
        const system =
            marquee.apiSystem === "user"
                ? marquee.systemName
                : marquee.apiSystem;
        toast.promise(updateMarquee(system, marquee.filename, streamImages), {
            loading: "Updating Marquee",
            success: "Successfully Updated Marquee",
            error: "Couldn't set the marquee",
        });
    };
    const { onClick = setMarquee } = props;
    const showMarquee = () => {
        switch (marquee.id) {
            case "bad":
                return <div />;
            default:
                return (
                    <React.Fragment>
                        {error ? (
                            <div
                                className="grid-item fade-in"
                                style={{
                                    backgroundColor: "#4c3ba2",
                                    flexDirection: "column",
                                    justifyContent: "center",
                                    cursor: "pointer",
                                    userSelect: "none",
                                    fontWeight: 600,
                                    minHeight: 60,
                                }}
                                onClick={() => onClick()}
                            >
                                <p>{startCase(marquee.name)}</p>
                                {marquee.id === "user" && (
                                    <span
                                        style={{
                                            position: "absolute",
                                            bottom: "0px",
                                            right: "0px",
                                            backgroundColor: "rgba(0,0,0,.65)",
                                            padding: "2px 4px",
                                            borderRadius: "4px 0 0 0",
                                        }}
                                    >
                                        {startCase(marquee.systemName)}
                                    </span>
                                )}
                            </div>
                        ) : (
                            <LazyLoad
                                height={60}
                                style={{ position: "relative" }}
                            >
                                <img
                                    className="grid-item fade-in"
                                    src={`${getDeviceUrl()}/thumbnails/${marquee.apiSystem}/${marquee.filename}`}
                                    alt={marquee.filename}
                                    onError={() => setError(true)}
                                    style={{
                                        backgroundColor:
                                            "rgba(255, 255, 255, .05)",
                                        cursor: "pointer",
                                        position: "relative",
                                        minHeight: 40,
                                    }}
                                    onClick={() => setMarquee()}
                                />
                                <div
                                    style={{
                                        position: "absolute",
                                        top: "4px",
                                        right: "4px",
                                        backgroundColor: "rgba(0,0,0,.7)",
                                        borderRadius: "50%",
                                        width: "24px",
                                        height: "24px",
                                        display: "flex",
                                        alignItems: "center",
                                        justifyContent: "center",
                                        cursor: "pointer",
                                        border: "1px solid rgba(255,255,255,.3)",
                                        transition: "all 0.2s ease",
                                    }}
                                    onClick={(e) => {
                                        e.stopPropagation();
                                        setModalOpen(true);
                                    }}
                                    onMouseEnter={(e) => {
                                        e.currentTarget.style.backgroundColor = "rgba(107,0,120,1)";
                                        e.currentTarget.style.transform = "scale(1.1)";
                                    }}
                                    onMouseLeave={(e) => {
                                        e.currentTarget.style.backgroundColor = "rgba(0,0,0,.7)";
                                        e.currentTarget.style.transform = "scale(1)";
                                    }}
                                >
                                    <span style={{
                                        color: "#fff",
                                        fontSize: "14px",
                                        fontWeight: "bold",
                                        fontStyle: "italic"
                                    }}>
                                        i
                                    </span>
                                </div>
                                {marquee.id === "user" && (
                                    <span
                                        style={{
                                            fontWeight: 600,
                                            position: "absolute",
                                            bottom: "0px",
                                            right: "0px",
                                            backgroundColor: "rgba(0,0,0,.65)",
                                            padding: "1px px",
                                            borderRadius: "4px 0 0 0",
                                        }}
                                    >
                                        {startCase(marquee.systemName)}
                                    </span>
                                )}
                            </LazyLoad>
                        )}
                    </React.Fragment>
                );
        }
    };

    return (
        <React.Fragment>
            {showMarquee()}
            <MarqueeDetailsModal
                open={modalOpen}
                onClose={() => setModalOpen(false)}
                marquee={marquee}
                system={props.system}
            />
        </React.Fragment>
    );
};

export default React.memo(MarqueeItem);
