import React, { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { ConsoleData } from "../../types/AppContext";
import Loading from "../Loading";
import { readConsoles } from "../../services";

const ConsoleList: React.FC = () => {
    const [loading, setLoading] = useState<boolean>(false);
    const [consoles, setConsoles] = useState<ConsoleData[]>([]);
    const nav = useNavigate();

    useEffect(() => {
        const loadConsoles = async () => {
            setLoading(true);
            await readConsoles()
                .then((value) => {
                    setConsoles(value.consoles);
                })
                .catch()
                .finally(() => {
                    setLoading(false);
                });
        };
        loadConsoles();
    }, []);

    const renderList = () => {
        return consoles.map((item, index) => {
            return (
                <div
                    className="fade-in"
                    key={index}
                    style={{
                        backgroundColor: "#456",
                        borderRadius: "4px",
                        padding: ".5rem",
                        border: "1px solid #ABABAB",
                        color: "#FFF",
                        cursor: "pointer",
                    }}
                    onClick={() => {
                        nav(`/marquees/${item.name}`);
                    }}
                >
                    <p>{item.descriptiveName}</p>
                    <p>{item.numMarquees} Marquees</p>
                </div>
            );
        });
    };

    return (
        <React.Fragment>
            <Loading isLoading={loading} />
            <div className="layout-grid">{renderList()}</div>
        </React.Fragment>
    );
};

export default ConsoleList;
