import React, { useEffect, useState, useRef } from "react";
import toast from "react-hot-toast";
import DefaultOne from "../../assets/default-01.png";
import DefaultTwo from "../../assets/default-02.png";
import DefaultThree from "../../assets/default-03.png";
import { readConsoleMarquees, updateDefaultMarquee, updateMarquee } from "../../services";
import { MarqueeInfo } from "../../types/DeviceInfo.type";
import Loading from "../Loading";
import MarqueeItem from "../MarqueeItem";
import { useAppSettings } from "../../App";

interface Resolution {
    width: number;
    height: number;
}

interface ContextMenuProps {
    x: number;
    y: number;
    onDelete: () => void;
    onClose: () => void;
}

const ContextMenu: React.FC<ContextMenuProps> = ({ x, y, onDelete, onClose }) => {
    useEffect(() => {
        const handleClickOutside = () => onClose();
        document.addEventListener('click', handleClickOutside);
        return () => document.removeEventListener('click', handleClickOutside);
    }, [onClose]);

    return (
        <div 
            className="fixed bg-white shadow-lg rounded-lg py-1 w-24 z-50"
            style={{ 
                left: x + 10, 
                top: y,
                backgroundColor: 'rgb(255, 0, 0)',
                border: '1px solid rgb(55, 65, 81)',
                position: 'fixed'
            }}
            onClick={(e) => e.stopPropagation()}
        >
            <div 
                className="px-4 py-2 text-sm text-gray-200 hover:bg-gray-700 cursor-pointer"
                onClick={onDelete}
            >
                Delete
            </div>
        </div>
    );
};

const DefaultMarquee: React.FC = () => {
    const [loading, setLoading] = useState<boolean>(false);
    const [marqueeData, setMarqueeData] = useState<MarqueeInfo[]>([]);
    const [resolution, setResolution] = useState<Resolution>({ width: 1920, height: 360 });
    const { streamImages } = useAppSettings();
    const fileInputRef = useRef<HTMLInputElement>(null);
    const [contextMenu, setContextMenu] = useState<{ x: number; y: number; filename: string } | null>(null);

    const fetchResolutionInfo = async () => {
        try {
            const hostname = window.location.hostname;
            const response = await fetch(`http://${hostname}:8080/v2/info`);
            if (!response.ok) throw new Error('Failed to fetch device info');
            const data = await response.json();
            if (data.resolution) {
                const height = data.resolution.width === 1280 ? 390 : 360;
                setResolution({ width: data.resolution.width, height });
            }
        } catch (error) {
            console.error('Error fetching device info:', error);
            setResolution({ width: 1920, height: 360 });
        }
    };

    const fetchMarqueeData = async () => {
        setLoading(true);
        try {
            const data = await readConsoleMarquees("user");
            const { marquees } = data;
            const filteredMarquees = marquees.filter((marquee: MarqueeInfo) => 
                marquee.filename && marquee.systemName && !marquee.systemName.startsWith('._')
            );
            setMarqueeData(filteredMarquees);
        } catch (error) {
            console.error("Error loading marquees:", error);
            toast.error("Failed to load marquees");
        }
        setLoading(false);
    };

    const handleContextMenu = (event: React.MouseEvent, filename: string) => {
        event.preventDefault();
        const rect = (event.currentTarget as HTMLElement).getBoundingClientRect();
        const windowWidth = window.innerWidth;
        
        // Calculate x position - if too close to right edge, show menu to the left
        let xPosition = rect.right + 10;
        if (rect.right + 100 > windowWidth) { // 100px is approximate menu width
            xPosition = rect.left - 110; // Position menu to the left of the element
        }
        
        setContextMenu({
            x: xPosition,
            y: rect.top,
            filename: filename
        });
    };

    async function handleDeleteMarquee(filename: string) {
        const hostname = window.location.hostname;
        try {
            console.log('Attempting to delete:', filename); // Debug log
            const response = await fetch(
                `http://${hostname}:8080/v2/delete?${encodeURIComponent(filename)}`,
                { 
                    method: 'DELETE',
                    headers: {
                        'Accept': 'application/json',
                        'Access-Control-Allow-Methods': 'DELETE'
                    }
                }
            );
            
            console.log('Delete response status:', response.status); // Debug log
            const responseText = await response.text();
            console.log('Delete response:', responseText); // Debug log
            
            if (!response.ok) {
                throw new Error(`Delete failed: ${responseText}`);
            }
            
            toast.success('Marquee deleted successfully');
            await fetchMarqueeData();
        } catch (e) {
            console.error('Delete error:', e);
            toast.error('Failed to delete marquee');
        }
        setContextMenu(null);
    }

    const handleDefaultMarquee = async (value: string | number) => {
        if (typeof value === 'number') {
            await toast.promise(
                updateDefaultMarquee(value),
                {
                    loading: "Setting default marquee",
                    success: "Successfully set default marquee",
                    error: "Couldn't set the default marquee"
                }
            );
        }

        if (typeof value === 'string') {
            const filename = value.split('/').pop();
            if (filename) {
                await toast.promise(
                    updateDefaultMarquee(filename.replace('.jpg', '')),
                    {
                        loading: "Setting default marquee",
                        success: "Successfully set default marquee",
                        error: "Couldn't set the default marquee"
                    }
                );
            }
        }

        const system = "user";
        const filename = `default${value}`;
        await toast.promise(
            updateMarquee(system, filename, streamImages),
            {
                loading: "Updating Marquee",
                success: "Successfully Updated Marquee",
                error: "Couldn't set the marquee"
            }
        );
    };

    const validateFilename = (filename: string) => {
        const pattern = /^[a-zA-Z0-9]+-[a-zA-Z0-9]+\.jpg$/i;
        return pattern.test(filename);
    };

    const validateImage = (file: File): Promise<boolean> => {
        return new Promise((resolve) => {
            const img = new Image();
            img.onload = () => resolve(img.width === resolution.width && img.height === resolution.height);
            img.onerror = () => resolve(false);
            img.src = URL.createObjectURL(file);
        });
    };

    const handleFileUpload = async (event: React.ChangeEvent<HTMLInputElement>) => {
        const file = event.target.files?.[0];
        if (!file) return;

        if (!file.type.includes('jpeg') && !file.type.includes('jpg')) {
            toast.error('Please upload a JPG file');
            return;
        }

        if (!validateFilename(file.name)) {
            toast.error('Filename must be in format x-y.jpg (e.g., default-sega.jpg)');
            return;
        }

        const isValidDimensions = await validateImage(file);
        if (!isValidDimensions) {
            toast.error(`Image must be exactly ${resolution.width}x${resolution.height} pixels`);
            return;
        }

        const formData = new FormData();
        formData.append('file', file);

        try {
            const response = await fetch('/v2/marquees/user', {
                method: 'POST',
                body: formData
            });

            if (!response.ok) throw new Error('Upload failed');

            await updateMarquee("user", file.name.replace('.jpg', ''), streamImages);
            await fetchMarqueeData();
            toast.success('Marquee uploaded successfully');
            event.target.value = '';
        } catch (error) {
            console.error('Upload error:', error);
            toast.error('Failed to upload marquee');
        }
    };

    useEffect(() => {
        fetchResolutionInfo();
        fetchMarqueeData();
    }, []);

    return (
        <React.Fragment>
            <div style={{ marginLeft: '30px', marginBottom: '10px' }}>
                <button
                    onClick={() => fileInputRef.current?.click()}
                    style={{
                        padding: '8px 16px',
                        backgroundColor: '#4CAF50',
                        color: 'white',
                        border: 'none',
                        borderRadius: '4px',
                        cursor: 'pointer'
                    }}
                >
                    Upload Marquee
                </button>
                <input
                    ref={fileInputRef}
                    type="file"
                    accept=".jpg,.jpeg"
                    onChange={handleFileUpload}
                    style={{ display: 'none' }}
                />
            </div>
            <div style={{ marginLeft: '30px', marginBottom: '20px' }}>
                <p style={{ color: '#d1d5db', fontSize: '14px' }}>
                    Note: Custom marquee files must follow the naming convention: <span style={{ background: 'rgba(0,0,0,0.3)', padding: '4px 8px', borderRadius: '4px', fontFamily: 'monospace' }}>default-yourfilename.jpg</span>
                </p>
            </div>
            <div style={{ marginLeft: '30px', marginBottom: '20px' }}>
                <p style={{ color: '#d1d5db', fontSize: '14px' }}>
                    Select desired marquee and it will appear by default on startup 
                </p>
            </div>
            <Loading isLoading={loading} />
            <div className="layout-grid fade-in">
                <img
                    onClick={() => handleDefaultMarquee(1)}
                    style={{
                        backgroundColor: "rgba(255, 255, 255, .05)",
                        cursor: "pointer",
                        position: "relative",
                        minHeight: 40,
                    }}
                    src={DefaultOne}
                    alt="marquee 1"
                    className="grid-item fade-in"
                />
                <img
                    onClick={() => handleDefaultMarquee(2)}
                    style={{
                        backgroundColor: "rgba(255, 255, 255, .05)",
                        cursor: "pointer",
                        position: "relative",
                        minHeight: 40,
                    }}
                    src={DefaultTwo}
                    alt="marquee 2"
                    className="grid-item fade-in"
                />
                <img
                    onClick={() => handleDefaultMarquee(3)}
                    style={{
                        backgroundColor: "rgba(255, 255, 255, .05)",
                        cursor: "pointer",
                        position: "relative",
                        minHeight: 40,
                    }}
                    src={DefaultThree}
                    alt="marquee 3"
                    className="grid-item fade-in"
                />
                {marqueeData.map((item: MarqueeInfo) => (
                    <div 
                        key={item.filename}
                        onContextMenu={(e) => {
                            // Construct the complete filename with prefix and extension
                            const completeFilename = `${item.systemName}-${item.filename}.jpg`;
                            console.log('Full MarqueeInfo item:', item);
                            console.log('Constructed filename for delete:', completeFilename);
                            handleContextMenu(e, completeFilename);
                        }}
                    >
                        <MarqueeItem
                            system={"user"}
                            marquee={item}
                            onClick={() => handleDefaultMarquee(item.filename)}
                        />
                    </div>
                ))}
            </div>
            {contextMenu && (
                <ContextMenu
                    x={contextMenu.x}
                    y={contextMenu.y}
                    onDelete={() => handleDeleteMarquee(contextMenu.filename)}
                    onClose={() => setContextMenu(null)}
                />
            )}
        </React.Fragment>
    );
};

export default DefaultMarquee;