import React, { useState } from "react";
import "./App.css";
import { HashRouter, Route, Routes } from "react-router-dom";
import Header from "./components/Header";
import ConsoleList from "./components/ConsoleList";
import MarqueeList from "./components/MarqueeList";
import StickyBox from "react-sticky-box";
import MainMenu from "./components/MainMenu";
import DefaultMarquee from "./components/DefaultMarquee";
import DeviceSettings from "./components/DeviceSettings";
import SendText from "./components/SendText";

export type ContextType = {
    streamImages: boolean;
    setStreamImages: Function;
};

export const ApplicationContext = React.createContext<ContextType>(
    {} as ContextType
);
export const useAppSettings = () => React.useContext(ApplicationContext);

const App: React.FC = () => {
    const [streamImages, setStreamImages] = useState<boolean>(false);

    const state = {
        streamImages,
        setStreamImages,
    };
    return (
        <ApplicationContext.Provider value={state}>
            <HashRouter>
                <StickyBox style={{ zIndex: 100 }}>
                    <Header />
                </StickyBox>
                <Routes>
                    <Route path="/" element={<MainMenu />} />,
                    <Route path="/consoles" element={<ConsoleList />} />
                    ,
                    <Route path="/marquees/:system" element={<MarqueeList />} />
                    ,
                    <Route
                        path="/default-marquee"
                        element={<DefaultMarquee />}
                    />
                    ,
                    <Route path="/settings" element={<DeviceSettings />} />
                    ,
                    <Route path="/text" element={<SendText />} />
                </Routes>
            </HashRouter>
        </ApplicationContext.Provider>
    );
};

export default App;
