import axios from "axios";
import { getDeviceUrl } from ".";

export const toggleVideoMarquees = (value: boolean): Promise<any> => {
    const requestUrl = `${getDeviceUrl(
        ""
    )}/settings?key=ImageMarqueesOnly&value=${value ? "yes" : "no"}`;

    return axios.get(requestUrl).then((value) => {
        return value.data;
    });
};

