import { FormControlLabel, Switch } from "@material-ui/core";
import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useAppSettings } from "../../App";
import { useDebounce } from "../../hooks/useDebounce";
import { readConsoleMarquees, readFilesystemMarquees } from "../../services";
import { emptyMarquee, MarqueeInfo } from "../../types/DeviceInfo.type";
import Loading from "../Loading";
import MarqueeItem from "../MarqueeItem";

const MarqueeList: React.FC = () => {
    const [loading, setLoading] = useState<boolean>(false);
    const [marqueeData, setMarqueeData] = useState<MarqueeInfo[]>([]);
    const { system } = useParams();
    const [searchTerm, setSearchTerm] = useState<string>("");
    const debouncedSearchTerm = useDebounce(searchTerm, 350);
    const { streamImages, setStreamImages } = useAppSettings();

    useEffect(() => {
        if (system) {
            setLoading(true);

            // Fetch both JSON marquees and filesystem marquees
            Promise.all([
                readConsoleMarquees(system).catch(() => ({ marquees: [] })),
                readFilesystemMarquees(system).catch(() => ({ marquees: [] }))
            ])
                .then(([jsonData, filesystemData]) => {
                    const jsonMarquees = jsonData.marquees || [];
                    const filesystemMarquees = filesystemData.marquees || [];

                    // Create a map of filenames from JSON data to avoid duplicates
                    const jsonFilenames = new Set(
                        jsonMarquees.map((m: MarqueeInfo) => m.filename.toLowerCase())
                    );

                    // Filter out filesystem marquees that already exist in JSON
                    const uniqueFilesystemMarquees = filesystemMarquees.filter(
                        (m: MarqueeInfo) => !jsonFilenames.has(m.filename.toLowerCase())
                    );

                    // Merge both arrays
                    const allMarquees = [...jsonMarquees, ...uniqueFilesystemMarquees];

                    setMarqueeData(allMarquees);
                })
                .finally(() => {
                    setLoading(false);
                });
        }
    }, [system]);

    const filterdData = () => {
        let data = marqueeData?.filter((marquee) => {
            const regex = RegExp(`${debouncedSearchTerm}`, "i");
            return regex.test(marquee.name.toLowerCase().trim());
        });
        if (!data) {
            data = [];
        }
        while (data.length < 10) {
            data.push(emptyMarquee());
        }
        return data;
    };

    return (
        <React.Fragment>
            <Loading isLoading={loading} />
            {!loading && (
                <div className="layout-grid fade-in">
                    <input
                        value={searchTerm}
                        onChange={(e) => {
                            setSearchTerm(e.target.value);
                        }}
                        placeholder="Search..."
                        type="text"
                        id="fname"
                        name="fname"
                        style={{
                            minHeight: 54,
                            border: "none",
                            borderRadius: 4,
                            backgroundColor: "antiquewhite",
                            padding: "0 1rem",
                            fontSize: "1.2rem",
                            width: "100%",
                            boxSizing: "border-box",
                        }}
                    />
                     {/* <div
                        style={{
                            color: " rgba(107,0,120,1)",
                            backgroundColor: "antiquewhite",
                            padding: ".5rem 0 .5rem 1rem",
                            borderRadius: "4px",
                        }}
                    >
                     <FormControlLabel
                            control={
                                <Switch
                                    checked={streamImages}
                                    onChange={() =>
                                        setStreamImages(!streamImages)
                                    }
                                    color="primary"
                                />
                            }
                            label={<span>Stream Marquees to Panel</span>}
                        /> 
                    </div>*/}
                </div>
            )}

            <div className="layout-grid fade-in">
                {filterdData().map((marquee, index) => {
                    return (
                        <MarqueeItem
                            key={index}
                            system={system!}
                            marquee={marquee}
                        />
                    );
                })}
            </div>
        </React.Fragment>
    );
};

export default MarqueeList;

