import { Button } from "@material-ui/core";
import React, { useState } from "react";
import toast from "react-hot-toast";
import { setScrollingText } from "../../services";

const SendText: React.FC = () => {
    const [text, setText] = useState<string>("");
    return (
        <div className="layout-grid fade-in" style={{ marginTop: "0.5rem" }}>
            <div
                className="settings"
                style={{ display: "flex", flexDirection: "column" }}
            >
                <label style={{ margin: "1rem 0 .5rem 0" }}>
                    Scrolling Text
                </label>

                <input
                    style={{
                        height: 36,
                        fontSize: "1.2rem",
                        padding: "0 0.4rem",
                    }}
                    type="text"
                    id="fname"
                    name="fname"
                    value={text}
                    onChange={(event) => {
                        setText(event.target.value);
                    }}
                />
                <div
                    style={{
                        display: "grid",
                        gridTemplateColumns: "1fr 2fr",
                        gridGap: "1rem",
                    }}
                >
                    <div />
                    <Button
                        style={{ marginTop: "1.2rem" }}
                        variant="contained"
                        color="primary"
                        onClick={() => {
                            toast.promise(
                                setScrollingText(text),
                                {
                                    loading: "Sending message to marquee",
                                    success: "Message sent",
                                    error: "Failed",
                                },
                                { duration: 4000 }
                            );
                        }}
                    >
                        Send Text
                    </Button>
                </div>
            </div>
        </div>
    );
};

export default SendText;
