export const updateMarquee = (
    console: string,
    marquee: string,
    stream: boolean
) => {
    const requestUrl = `${getDeviceUrl("")}/arcade/stream/${console}/${marquee}`;

    return axios.post(requestUrl);
};

export const updateDefaultMarquee = (id: string | number) => {
    const requestUrl = `${getDeviceUrl()}/utility/marquee/set/${id}`;
    return axios.get(requestUrl);
};

export const setScrollingText = (text: string) => {
    const requestUrl = `${getDeviceUrl("")}/text?t=${text}`;
    return axios.get(requestUrl);
};

export const readFilesystemMarquees = (console: string): Promise<any> => {
    const requestUrl = `${getDeviceUrl()}/filesystem/marquees/${console}`;
    return fetch(requestUrl).then((response) => response.json());
};

export const checkForUpdates = (): Promise<any> => {
    const requestUrl = `${getDeviceUrl()}/check-update`;
    return axios.get(requestUrl).then((value) => value.data);
};
